import Vue from 'vue';
import { Button } from 'element-ui';
import config from './typeConfig';
Vue.use(Button);
export default Vue.extend({
    props: {
        type: {
            type: String,
            default: '404'
        },
        title: String,
        desc: String,
        img: String
    },
    data() {
        return {
            config: config
        };
    },
    computed: {
        pageType() {
            return this.type in this.config ? this.type : '404';
        }
    }
});



